.container {
	width: 100%;
	min-height: 100vh;

	background: #000;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}

.text {
	padding: 0 var(--page-side-padding);

	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;

	.heading {
		font-size: 3em;
		margin: 2rem auto;
	}

	.paragraph {
		font-size: 1.5em;
		margin-top: 3rem;
	}

	.images {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 20%;

		.left,
		.right {
			width: 40%;

			.image {
				width: 100%;
			}

			h3 {
				text-align: center;
			}
		}
	}

